/*===== Адаптация Hero.css =====*/
@media (max-width: 768px) {
    .hero h1 {
        font-size: 25px;
    }
    .hero p {
        font-size: 15px;
    }
}

/*===== Адаптация Build.css =====*/
@media (max-width: 768px) {
    .title {
        font-size: 25px;
    }    
    .subtitle {
        font-size: 15px;
    }

    .form-row {
        width: 60%;
    }
}

/*===== Адаптация Features.css =====*/
@media (max-width: 768px) {
    .features-row {
        flex-direction: column;
        align-items: center;
    }
    .feature {
        max-width: 100%;
        margin-bottom: 40px;
    }
}

@media (max-width: 480px) {
    .feature h2 {
        font-size: 16px;
    }
    .feature p {
        font-size: 12px;
    }
}