.features-section {
    padding: 50px 0;
    background-color: #fff;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
}

.features-row {
    display: flex;
    justify-content: space-around;
    max-width: 1200px;
    width: 100%;
}

.feature {
    text-align: center;
    max-width: 300px;
}

.feature svg {
    margin-bottom: 20px;
}

.feature h2 {
    font-size: 18px;
    font-weight: bold;
    color: #111827; 
    margin-bottom: 10px;
}
.feature p {
    font-size: 14px;
    color: #6b7280; 
    line-height: 1.6;
}
